<template>
  <div class="product">
    <div class="product-list">
      <app-pos-purchase-item
        v-for="(product, index) in posOrders"
        :product="product"
        @click.native="openPosDialogProductUpdate(index, product)"
        :key="product.product_id"></app-pos-purchase-item>
    </div>

    <div class="total-purchase" @click="showPaymentDialog">
      {{ totalOrderValue | toIdr }}
    </div>

    <div id="printJS-receipt">
      <div style="text-align:center">{{shop_name}}</div>
      <table width="100%" style="margin-top:50px;">
        <tr>
          <td></td>
          <td style="text-align:right">{{ new Date() | moment("ddd, D/MM/YYYY") }}</td>
        </tr>
      </table>
      <table width="100%">
        <!-- Header -->
        <tr>
          <th style="border-top:1px dashed black;border-bottom:1px dashed black;pading:10px 0;">Qty</th>
          <th style="border-top:1px dashed black;border-bottom:1px dashed black;pading:10px 0;">Item</th>
          <th style="border-top:1px dashed black;border-bottom:1px dashed black;pading:10px 0;">Price</th>
        </tr>

        <!-- Body -->
        <template v-for="product in posOrders">
          <tr :key="product.product_id">
            <td width="10%" style="text-align:center;padding:5px 0;">{{ product.qty }}</td>
            <td width="60%" style="padding:5px 0;">{{ product.name }} hello kity super man</td>
            <td width="30%" style="text-align:right;padding:5px 0;">{{ calcTotalPrice(product.qty,product.selectedPrice) | toIdr }}</td>
          </tr>
          <tr :key="product.product_id"
            v-if="calcDiscountValue(calcDiscountValue(product.qty,product.selectedPrice), product.discount) !== 0">
            <td colspan="2" style="text-align:right;padding:2px 0;">Diskon</td>
            <td style="text-align:right;padding:2px 0;">{{ calcDiscountValue(calcDiscountValue(product.qty,product.selectedPrice), product.discount) | toIdr }}</td>
          </tr>
        </template>

        <!-- Foot -->
        <tr style="font-weight:bold">
          <td colspan="2" style="border-top:1px dashed black;padding:2px 0;">Total</td>
          <td style="text-align:right;border-top:1px dashed black;padding:2px 0;">{{ totalOrderValue | toIdr }}</td>
        </tr>
        <tr>
          <td colspan="2" style="padding:2px 0;">Cash</td>
          <td style="text-align:right;padding:2px 0;">{{ totalPayment | toIdr }}</td>
        </tr>
        <tr>
          <td colspan="2" style="padding:2px 0;">Change</td>
          <td style="text-align:right;padding:2px 0;">{{ change | toIdr }}</td>
        </tr>
      </table>

      <table width="100%" style="margin-top:50px;">
        <tr>
          <td style="text-align:center">Terima kasih. Selamat berbelanja kembali.</td>
        </tr>
      </table>
    </div>

    <app-pos-dialog-product-update
      :visible="posDialogProductUpdate.visible"
      :product="posDialogProductUpdate.product"
      :index="posDialogProductUpdate.index"
      @closeDialog="closePosDialogProductUpdate"
      v-if="posDialogProductUpdate.visible"
    ></app-pos-dialog-product-update>

    <app-pos-dialog-payment
      :title="'Payment'"
      :visible="paymentDialog.visible"
      :on-cancel="handleCloseDialog"
      :on-confirm="pay"
      :total-order="totalOrderValue"
    ></app-pos-dialog-payment>

    <app-pos-dialog-receipt
      :title="'Kemabilan'"
      :visible="receiptDialog.visible"
      :on-cancel="handleCloseDialogReceipt"
      :on-confirm="printReceipt"
      :change="change"
    ></app-pos-dialog-receipt>
  </div>
</template>

<script>
import { mapGetters, mapState } from "vuex";
import PosPurchaseItem from "./PosPurchaseItem";
import { calcTotalOrderValue } from "@/api/pos";
import PosDialogPayment from "@/components/Pos/PosDialogPayment";
import PosDialogReceipt from "@/components/Pos/PosDialogReceipt";
import PosDialogProductUpdate from "@/components/Pos/PosDialogProductUpdate";
import { getSelectedShopId, getSelectedShopName } from "@/utils/localStorage";
import { calcTotalPrice, calcDiscountValue } from "@/api/pos";

export default {
  components: {
    appPosPurchaseItem: PosPurchaseItem,
    appPosDialogPayment: PosDialogPayment,
    appPosDialogReceipt: PosDialogReceipt,
    appPosDialogProductUpdate: PosDialogProductUpdate,
  },
  computed: {
    ...mapGetters("pos", ["posOrders"]),
    ...mapState("pos", {
      totalPayment: state => state.totalPayment,
      totalOrderValue: state => state.totalOrderValue,
    })
  },
  data() {
    return {
      keyword: "",
      // totalOrderValue: 0,
      paymentDialog: {
        visible: false
      },
      receiptDialog: {
        visible: false
      },
      change: 0,

      calcTotalPrice,
      calcDiscountValue,

      shop_name: getSelectedShopName(),

      // Dialog product update
      posDialogProductUpdate: {
        visible: false,
        product: null,
        index: -1,
      },
    };
  },
  methods: {
    showPaymentDialog() {
      if (this.totalOrderValue === 0) {
        this.$message.error("Pilih item terlebih dahulu!");
      } else {
        this.paymentDialog.visible = true;
      }
    },

    // Payment dialog handler
    handleCloseDialog(done) {
      this.paymentDialog.visible = false;
    },
    pay() {
      const payload = {
        shop_id: getSelectedShopId(),
        payment: this.totalPayment,
        products: this.posOrders.map(order => {
          return {
            product_id: order.productId,
            price: order.selectedPrice,
            quantity: order.qty,
            discount_type: order.discount.type,
            discount_value: order.discount.value
          };
        })
      };
      this.$store
        .dispatch("pos/SellProduct", payload)
        .then(res => {
          // calculate change
          const totalOrderValue = calcTotalOrderValue(this.posOrders);
          this.change = this.totalPayment - totalOrderValue;
          // this.$message.success(res.data.messages[0]);
          this.paymentDialog.visible = false;
          this.receiptDialog.visible = true;
        })
        .catch(error => {
          const errorMsg = error.response.data.messages;
          errorMsg.forEach(msg => {
            this.$message.error(msg);
          });
          this.loading = false;
        });
    },

    // Receipt handler
    handleCloseDialogReceipt(done) {
      this.resetPos();
      this.receiptDialog.visible = false;
    },
    printReceipt() {
      const iframe = top.frames["posReceiptIframe"].document;
      const height = (document.querySelector("#printJS-receipt").offsetHeight + 0) + 'px';
      iframe.open();
      iframe.write(document.querySelector("#printJS-receipt").outerHTML);
      var css = '@media print {' +
        '@page { size: 70mm '+ height +'; margin: 0; }' +
        '#printJS-receipt table {font-size:7pt !important;}' +
      '}';
      iframe.head.innerHTML = '<style type="text/css">' + css + "</style>";
      console.log(iframe.head.innerHTML);
      // iframe.close();
      // this.$nextTick(() => print());
      document.getElementById("pos-receipt-iframe").contentWindow.print();
    },

    // [Handler] Pos Product Dialog Update
    openPosDialogProductUpdate(index, product) {
      this.posDialogProductUpdate = {
        visible: true,
        product,
        index,
      }
      console.log("clicked...", this.posDialogProductUpdate);
    },
    closePosDialogProductUpdate() {
      this.posDialogProductUpdate = {
        visible: false,
        product: null,
        index: -1,
      }
    },

    // Common
    resetPos() {
      this.$store.dispatch("pos/ResetPos");
      this.change = 0;
    }
  }
};
</script>

<style lang="scss" scoped>
.product {
  display: flex;
  flex-direction: column;
  height: 100%;

  .product-list {
    height: 100%;
    overflow-y: auto;
  }

  .total-purchase {
    font-size: 32pt;
    font-weight: bold;
    padding: 10px;
    text-align: right;
    background: teal;
    color: white;
    cursor: pointer;
  }
  .total-purchase:hover {
    background: lightblue;
  }
}
.padding-6 {
  padding: 6px !important;
}

#printJS-receipt {
  font-family: "monospace";
  opacity: 0;
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
}

</style>




