<template>
  <div class="product">

    <div class="product__search">
      <el-input
        placeholder="Search"
        suffix-icon="el-icon-search"
        v-model="urlParams.s"
        class="input-with-select"
        @keyup.enter.native="getProducts">
      </el-input>
    </div>

    <div class="product-list">
      <div v-if="loading" class="loading">
        <i class="el-icon-loading icon"></i> Memuat data...
      </div>

      <pos-product-item
        v-if="posProducts"
        v-for="product in posProducts"
        :product="product"
        @click.native="showDialogProductAdd(product)"
        :key="product.id"></pos-product-item>

      <div v-if="posProducts.length === 0" class="loading">Tidak ada produk</div>
    </div>

    <!-- Modal Add -->
    <pos-dialog-product-add></pos-dialog-product-add>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

import PosDialogProductAdd from "@/components/pos/PosDialogProductAdd";
import PosProductItem from "@/components/pos/PosProductItem";
import { getSelectedShopId } from "@/utils/localStorage";

export default {
  components: {
    PosProductItem,
    PosDialogProductAdd
  },
  computed: {
    ...mapGetters("pos", ["posProducts", "selectedShop"])
  },
  data() {
    return {
      keyword: "",
      loading: false,
      // selectedShop: this.$store.state.pos.selectedShop,

      // Dialog
      dialogProductAdd: {
        isOpen: false
      },

      // Get
      urlParams: {
        s: "",
        l: 10,
        p: 1,
        oby: "created_at", // order_by (product_id|name|description|stock|created_at)
        sort: "desc",
        shop_id: getSelectedShopId(),
        category_id: ""
      }
    };
  },
  methods: {
    // Get
    getProducts() {
      this.loading = true;
      this.$store.dispatch("pos/FetchPosProducts", this.urlParams).then(res => {
        const data = res.data;
        const messages = data.messages;
        const status_code = data.status_code;

        // Set global state
        this.$store.dispatch("pos/setProducts", data.data.products);
        this.loading = false;

        // set pagination
        this.pagination = {
          ...this.pagination,
          total: data.data.total
        };
      });
    },

    // Dialog
    showDialogProductAdd(product) {
      this.$store.dispatch("pos/togglePosDialogProductAdd");
      this.$store.dispatch("pos/setSelectedProduct", product);
      this.dialogProductAdd.isOpen = !this.dialogProductAdd.isOpen;
    }
  }
};
</script>

<style lang="scss" scoped>
.product {
  display: flex;
  flex-direction: column;
  height: 100%;

  > .product__search {
    padding: 5px;
  }
}
.product-list {
  height: 100%;
  overflow-y: auto;
}
.padding-6 {
  padding: 6px !important;
}
.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;

  .icon {
    margin-right: 5px;
  }
}
</style>




