export const IDRMaskMixin = {
  data() {
    return {
      money: {
        decimal: ',',
        thousands: '.',
        prefix: 'Rp ',
        suffix: ' ',
        precision: 0,
        masked: false /* doesn't work with directive */
      }
    };
  }
};



