<template>
  <el-dialog :title="posProductForm.name" :before-close="closePosDialogProductAdd" :visible="posDialogProductAdd.isOpen" :width="formDialog.dialog.width" v-if="posSelectedProduct">
    <el-form :model="posProductForm" :rules="posProductFormRules" ref="posProductForm" :label-position="formDialog.dialog.labelPosition" label-width="100px">

      <el-form-item label="Quantity" prop="qty">
        <el-input type="number" v-model.number="posProductForm.qty" min="0" :max="posProductForm.stock"  placeholder="stock"></el-input>
      </el-form-item>

      <el-form-item label="Stock" prop="stock">
        {{posProductForm.stock}}
      </el-form-item>

      <el-form-item :label="'Pilih Harga'">
        <el-radio-group v-model.number="posProductForm.selectedPrice">
          <el-radio-button
            v-for="(price, index) in posProductForm.prices"
            :key="index"
            :label="price.price"></el-radio-button>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="Diskon" prop="discount">
        <el-row :gutter="5">
          <el-col :sm="8">
            <el-select v-model="posProductForm.discount.type" placeholder="Select">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="8">
            <el-input type="number" v-model.number="posProductForm.discount.value" min="0" max="100"  placeholder="stock"></el-input>
          </el-col>
        </el-row>
      </el-form-item>

      <el-form-item>
        <el-button @click="closePosDialogProductAdd">{{$t('table.cancel')}}</el-button>
        <el-button type="primary" @click="addOrder">Add Order</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>

<script>
import { mapGetters } from "vuex";
import { dialog } from "@/responsive";

export default {
  computed: {
    ...mapGetters("pos", ["posDialogProductAdd", "posSelectedProduct"]),
    posProductForm() {
      return this.posSelectedProduct;
    }
  },
  data() {
    var checkQty = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("Please input the qty"));
      }
      if (!Number.isInteger(value)) {
        callback(new Error("Please input digits"));
      } else {
        if (value < 1) {
          callback(new Error("Minimum quantity 1"));
        } else {
          callback();
        }
      }
    };
    return {
      title: "Product",

      options: [
        {
          value: "value",
          label: "Price"
        },
        {
          value: "percent",
          label: "Percentage"
        },
      ],
      selectedPrice: null,

      // form
      posProductFormRules: {
        qty: [{ required: true, validator: checkQty, trigger: "blur" }]
      },

      // edit
      formDialog: {
        visible: false,
        status: "",
        selectedId: undefined,
        dialog: dialog.md
      }
    };
  },
  methods: {
    closePosDialogProductAdd() {
      this.$store.dispatch("pos/togglePosDialogProductAdd");
    },
    addOrder() {
      this.$refs.posProductForm.validate(valid => {
        if (valid) {
          {
            this.$store.dispatch("pos/addOrder", this.posProductForm);
            this.$store.dispatch("pos/togglePosDialogProductAdd");
          }
        }
      });
    }
  }
};
</script>



