<template>
  <el-row type="flex" justify="center" align="middle" class="product-item" v-if="product">
    <el-col :span="20" >{{product.name}}</el-col>
    <el-col :span="4" class="text-right">
      <i class="el-icon-star-off"></i>
    </el-col>
  </el-row>
</template>

<script>
export default {
  props: {
    product: Object,
  },
};
</script>

<style lang="scss" scoped>
.product-item {
  background: aliceblue;
  padding: 15px;
  border-bottom: 1px solid #eee;
}
.product-item:hover {
  background: lightblue;
  cursor: pointer;
}
</style>




