<template>
  <div class="category">

    <div class="category__search">
      <el-input
        placeholder="Cari kategori"
        v-model="keyword"
        suffix-icon="el-icon-search"
        @click.native="getProductCategories"
        clearable>
      </el-input>
    </div>

    <div class="product-list">
      <pos-product-item
        v-if="posCategories"
        v-for="productCategory in posCategories"
        :product="productCategory"
        @click.native="handleProductCategoryChanged(productCategory.category_id)"
        :key="productCategory.category_id"></pos-product-item>
    </div>
  </div>
</template>

<script>
import PosProductItem from "@/components/pos/PosProductItem";
import { mapGetters } from "vuex";

export default {
  components: {
    PosProductItem
  },
  computed: {
    ...mapGetters("pos", ["posCategories", "selectedShop"])
  },
  data() {
    return {
      keyword: "",
      loading: false,

      // Dialog
      dialogProductAdd: {
        isOpen: false
      },

      // Get
      urlParams: {
        s: "",
        l: 10,
        p: 1,
        oby: "created_at", // order_by (category_id|name|created_at)
        sort: "desc"
      },

      urlParamsProduct: {
        s: "",
        l: 10,
        p: 1,
        oby: "created_at", // order_by (product_id|name|description|stock|created_at)
        sort: "desc",
        shop_id: "",
        category_id: ""
      }
    };
  },
  methods: {
    getProductCategories() {
      this.loading = true;
      this.$store
        .dispatch("FetchProductCategories", this.urlParams)
        .then(res => {
          const data = res.data;
          const messages = data.messages;
          const status_code = data.status_code;

          this.$store.dispatch("pos/setProductCategories", data.data.categories);

          this.loading = false;

          // set pagination
          this.pagination = {
            ...this.pagination,
            total: data.data.total
          };
        });
    },
    getProducts() {
      this.loading = true;

      this.$store.dispatch("pos/FetchPosProducts", this.urlParamsProduct).then(res => {
        const data = res.data;
        const messages = data.messages;
        const status_code = data.status_code;

        // Set global state
        this.$store.dispatch("pos/setProducts", data.data.products);
        this.loading = false;

        // set pagination
        this.pagination = {
          ...this.pagination,
          total: data.data.total
        };
      });
    },
    handleProductCategoryChanged(categoryId) {
      this.urlParamsProduct = {
        ...this.urlParamsProduct,
        category_id: categoryId
      };
      this.getProducts();
    },
    initPos(shopId) {
      this.urlParamsProduct.shop_id = shopId;
      this.getProductCategories();
      this.getProducts();
    }
  },
  created() {
    const selectedShop = this.selectedShop;
    if (selectedShop) {
      this.initPos(selectedShop.shop_id);
    }
  },
  watch: {
    selectedShop(newShop) {
      const shopId = newShop.shop_id;
      this.initPos(shopId);
      this.urlParamsProduct.category_id = "";
    }
  }
};
</script>

<style lang="scss" scoped>
.category {
  display: flex;
  flex-direction: column;
  height: 100%;

  > .category__search {
    padding: 5px;
  }
}
.product-list {
  height: 100%;
  overflow-y: auto;
}
.padding-6 {
  padding: 6px !important;
}
</style>




