<template>
  <div style="height:100%">
    <iframe id="pos-receipt-iframe" name="posReceiptIframe" class="print" frameborder="0" style="width:100%;height:100% !important;"></iframe>
    <div class="pos-container">
      <el-row type="flex" align="middle" class="pos-header">
        <el-col :span="4" class="pos-header__col">
          <el-button size="small" icon="el-icon-back" @click="navigateToDashboard"></el-button>
        </el-col>
        <el-col :span="16" class="pos-header__col text-center">{{selectedShopName}}</el-col>
        <el-col :span="4" class="pos-header__col text-right">
          <el-dropdown>
            <el-button size="small" icon="el-icon-setting"></el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <div style="display:flex;align-items:center;">
                  <icon name="user" style="margin-right:5px;"></icon>
                  <span>{{username}}</span>
                </div>
              </el-dropdown-item>
              <el-dropdown-item divided>
                <div style="display:flex;align-items:center;" @click="handleChangeShop">
                  <icon name="home" style="margin-right:5px;"></icon>
                  <span>Pindah Toko</span>
                </div>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </el-col>
      </el-row>

      <el-row type="flex" class="fullHeight" :gutter="1">
        <el-col :span="4" class="fullHeight">
          <pos-product-category></pos-product-category>
        </el-col>
        <el-col :span="14" class="fullHeight">
          <pos-product-list></pos-product-list>
        </el-col>
        <el-col :span="6" class="fullHeight">
          <pos-purchase-list></pos-purchase-list>
        </el-col>
      </el-row>

      <!-- Choose shop dialog -->
      <el-dialog
        title="Pilih toko"
        :visible.sync="changeShopDialog.visible"
        :width="changeShopDialog.dialog.width"
        :before-close="handleCloseDialog">
        <el-form :model="shopDialogForm" :rules="shopDialogFormRules" ref="shopDialogForm" :label-position="changeShopDialog.dialog.labelPosition" label-width="100px">

          <el-form-item label="Pilih Toko" prop="selectedShop">
            <el-radio
              v-for="shop in shops"
              :key="shop.shop_id"
              v-model="shopDialogForm.selectedShop"
              :label="shop.shop_id"
              border>{{shop.name}}</el-radio>
          </el-form-item>

          <el-form-item>
            <el-button
              type="primary"
              :disabled="!shopDialogForm.selectedShop"
              @click="showChangeShopConfirmation">{{$t('table.confirm')}}</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>

      <!-- Change shop confirmation dialog -->
      <el-dialog
        title="Apakah yakin untuk pindah toko?"
        :visible.sync="changeShopConfirmationDialog.visible"
        :width="changeShopConfirmationDialog.width">
        <span>Bila anda pindah maka transaksi yang sedang berlangsung akan hilang</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="changeShopConfirmationDialog.visible = false">Cancel</el-button>
          <el-button type="primary" @click="changeShop">Setuju</el-button>
        </span>
      </el-dialog>

    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";
import { dialog } from "@/responsive";
import {
  PosProductCategory,
  PosProductList,
  PosPurchaseList
} from "@/components/Pos";
import { getUserShops, getIdentity } from "@/utils/auth";
import {
  getSelectedShop,
  getSelectedShopId,
  getSelectedShopName
} from "@/utils/localStorage";

const identity = getIdentity();

export default {
  components: {
    PosProductCategory,
    PosProductList,
    PosPurchaseList
  },
  computed: {
    // ...mapState("pos", {
    //   selectedShop: state => state.selectedShop
    // }),
    ...mapGetters("pos", ["selectedShop"])
  },
  data() {
    return {
      username: "",
      selectedShopName: "",

      // Form Data
      shopDialogForm: {
        selectedShop: null
      },
      shopDialogFormRules: {
        selectedShop: [{ required: true }]
      },

      // Dialog
      changeShopDialog: {
        visible: false,
        status: "",
        selectedId: undefined,
        dialog: dialog.md
      },
      changeShopConfirmationDialog: {
        visible: false,
        dialog: dialog.md
      },

      // Get
      shops: [],
      urlParamsShop: {
        s: "",
        l: 10,
        p: 1,
        oby: "created_at", // order_by (shop_id|name|address|phone|created_at)
        sort: "desc"
      }
    };
  },
  computed: {
    isSelectedShopSame() {
      const selectedShop = this.shopDialogForm.selectedShop;
      const currentShop = getSelectedShopId();

      console.log("selectedShop: ", selectedShop);
      console.log("currentShop: ", currentShop);

      if(currentShop === "") {
        return true
      }

      return selectedShop === currentShop;
    }
  },
  methods: {
    navigateToDashboard() {
      this.$router.push("/");
    },

    getUserShops() {
      this.shops = getUserShops();
    },

    handleChangeShop() {
      this.changeShopDialog.visible = true;
    },

    showChangeShopConfirmation() {
      this.changeShopDialog.visible = false;
      if (this.isSelectedShopSame) {
        this.changeShop();
      } else {
        console.log("shop tidak sama dengan sekarang...")
        this.changeShopConfirmationDialog.visible = true;
      }
    },
    changeShop() {
      this.changeShopConfirmationDialog.visible = false;
      const selectedShop = this.shops.filter(shop => {
        return shop.shop_id == this.shopDialogForm.selectedShop;
      })[0];
      console.log("shop yg terpilih ", selectedShop);
      this.$store.dispatch("pos/setSelectedShop", selectedShop);
      console.log("set shop");
      this.selectedShopName = selectedShop.name;
    },

    handleCloseDialog(done) {
      this.$message.error("Pilih toko terlebih dahulu");
    }
  },
  created() {
    // set username
    const identity = getIdentity();
    this.username = identity.username;

    // set selected shop
    const selectedShop = getSelectedShop();
    if (!selectedShop) {
      this.changeShopDialog.visible = true;
      console.log("pilih shop dulu...");
    } else {
      this.$store.dispatch("pos/setSelectedShop", selectedShop);
      console.log("set selected shop");
      this.shopDialogForm.selectedShop = selectedShop.shop_id;
      this.selectedShopName = getSelectedShopName();
    }
    this.getUserShops();
  }
};
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
@media print {
  @page {
    size: 70mm;
    margin: 0;
  }
  .print {
    // display: block !important;
    display: block !important;
  }
  .no-print {
    display: none !important;
  }
}
@media screen {
  .print {
    display: none !important;
  }
  .no-print {
    display: block !important;
  }
}
.pos-container {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.pos-header {
  height: 50px;
  background: teal;

  .pos-header__col {
    padding: 5px;
    font-weight: bold;
    font-size: 16pt;
    color: white;
  }
}
</style>



