<template>
  <el-dialog
    :title="_title"
    :visible="_visible"
    :width="width"
    :before-close="onCancel"
    center>
    <div style="text-align:center;margin:20px 0;">
      <span class="total-idr">{{change | toIdr}}</span>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="onCancel">Cancel</el-button>
      <el-button type="primary" @click="onConfirm">Print receipt</el-button>
    </span>
  </el-dialog>
</template>

<script>
import { arrayToObject } from "@/utils/arrayToObject";
import { dialogMixin } from "@/mixins/dialog";
import { IDRMaskMixin } from "@/mixins/currency/IDR";

const formFields = [
  "shop_id",
  "payment",
  [
    "products",
    [
      {
        product_id: null,
        price: 0,
        quantity: 0,
        discount_type: "percent",
        discount_value: 0
      }
    ]
  ]
];
export default {
  props: {
    change: {
      type: Number,
      required: true
    }
  },
  mixins: [dialogMixin, IDRMaskMixin],
  data() {
    return {
      formFields,
      posPaymentForm: arrayToObject(formFields),
      idr: [100000, 50000, 20000, 10000, 5000, 2000, 1000, 500, 200, 100],
      totalPayment: 0
    };
  },
  methods: {
    resetForm() {
      this.posPaymentForm = arrayToObject(this.formFields);
    },
    addMoney(money) {
      this.totalPayment = this.totalPayment + money;
      this.$store.dispatch("pos/setTotalPayment", this.totalPayment);
    }
  },
  watch: {
    totalPayment(newPayment) {
      console.log("WATCH total payment change to ", newPayment);
      this.$store.dispatch("pos/setTotalPayment", this.totalPayment);
    }
  }
};
</script>

<style scoped>
.money {
  display: flex;
  height: 80px;
  margin: 2px;
  justify-content: center;
  align-items: center;
  font-size: 14pt;
  border: 1px solid #eee;
  cursor: pointer;
}
.money:hover {
  background: #5867dd;
  color: white;
}
.total-idr {
  font-size: 32pt;
  text-align: center;
  border-width: medium;
}
</style>



