<template>
  <el-dialog
    :before-close="closeDialog"
    :visible="_visible"
    :title="'Update product'"
    :width="'50%'">
    <el-form :model="posProductForm" :rules="posProductFormRules" ref="posProductForm" :label-position="formDialog.dialog.labelPosition" label-width="100px">

      <el-form-item label="Quantity" prop="qty">
        <el-input type="number" v-model.number="posProductForm.qty" min="0" :max="posProductForm.stock"  placeholder="stock"></el-input>
      </el-form-item>

      <el-form-item label="Stock" prop="stock">
        {{posProductForm.stock}}
      </el-form-item>

      <el-form-item :label="'Pilih Harga'">
        <el-radio-group v-model.number="posProductForm.selectedPrice">
          <el-radio-button
            v-for="(price, index) in posProductForm.prices"
            :key="index"
            :label="price.price"></el-radio-button>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="Diskon" prop="discount">
        <el-row :gutter="5">
          <el-col :sm="8">
            <el-select v-model="posProductForm.discount.type" placeholder="Select">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="8">
            <el-input type="number" v-model.number="posProductForm.discount.value" min="0" max="100"  placeholder="stock"></el-input>
          </el-col>
        </el-row>
      </el-form-item>

      <el-form-item>
        <el-button @click="closeDialog">{{$t('table.cancel')}}</el-button>
        <el-button type="primary" @click="updateOrder">Update</el-button>
        <el-button type="danger" @click="deleteOrder">Delete</el-button>
      </el-form-item>
    </el-form>
  </el-dialog>
</template>

<script>
import { mapGetters } from "vuex";
import { dialog } from "@/responsive";

export default {
  props: {
    visible: {
      type: Boolean,
      required: true
    },
    product: {
      required: true
    },
    index: {
      type: Number,
      required: true
    }
  },
  computed: {
    _visible() {
      return this.visible;
    }
  },
  data() {
    var checkQty = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("Please input the qty"));
      }
      if (!Number.isInteger(value)) {
        callback(new Error("Please input digits"));
      } else {
        if (value < 1) {
          callback(new Error("Minimum quantity 1"));
        } else {
          callback();
        }
      }
    };
    return {
      // TODO:
      posProductForm: this.product,

      // FIXME:
      title: "Product",

      options: [
        {
          value: "value",
          label: "Price"
        },
        {
          value: "percent",
          label: "Percentage"
        }
      ],
      selectedPrice: null,

      // form
      posProductFormRules: {
        qty: [{ required: true, validator: checkQty, trigger: "blur" }]
      },

      // edit
      formDialog: {
        visible: false,
        status: "",
        selectedId: undefined,
        dialog: dialog.md
      }
    };
  },
  methods: {
    closeDialog() {
      this.$emit("closeDialog");
    },

    updateOrder() {
      // TODO: update by productId, selectedPrice, discount type and discount value
      const payload = {
        index: this.index,
        order: this.posProductForm
      };
      this.$store.dispatch("pos/updateOrder", payload);
      this.closeDialog();
    },

    deleteOrder() {
      console.log("Delete order");
      this.$store.dispatch("pos/deleteOrder", this.index);
      this.closeDialog();
    }
  }
};
</script>



