export const dialogMixin = {
  props: {
    title: { type: String, default: "Unknown dialog" },
    visible: { type: Boolean, required: true },
    onCancel: {
      type: Function,
      default: () => {
        this._visible = false;
      }
    },
    onConfirm: { type: Function, required: true }
  },
  computed: {
    _title() {
      return this.title;
    },
    _visible() {
      return this.visible;
    }
  },
  data() {
    return {
      width: "50%",
      dialogSize: {
        sm: "30%",
        md: "50%",
        lg: "90%",
      },
      windowWidth: window.innerWidth
    };
  },
  methods: {
    makeDialogResponsive() {
      if (this.windowWidth < 990) {
        this.width = this.dialogSize.lg;
      } else {
        this.width = this.dialogSize.md;
      }
    }
  },
  watch: {
    windowWidth(newWidth, oldWidth) {
      this.makeDialogResponsive();
    }
  },
  mounted() {
    let that = this;
    this.$nextTick(function() {
      window.addEventListener("resize", function(e) {
        that.windowWidth = window.innerWidth;
      });
    });
  }
};



