<template>
  <div class="product-item-detail">
    <el-row type="flex" justify="center" align="middle" class="product-item">
      <el-col :span="16">{{product.name}}</el-col>
      <el-col :span="2">{{product.qty}}</el-col>
      <el-col :span="6" class="text-right">
        {{totalPrice| toIdr}}
      </el-col>
    </el-row>
    <el-row class="text-right discount" v-if="discountValue !== 0">
      <el-col :span="18">Diskon</el-col>
      <el-col :span="6">{{discountValue | toIdr}}</el-col>
    </el-row>
  </div>
</template>

<script>
import { calcTotalPrice, calcDiscountValue } from "@/api/pos";
export default {
  props: {
    product: Object
  },
  computed: {
    discountValue() {
      const discount = this.product.discount;
      const totalPrice = calcTotalPrice(
        this.product.qty,
        this.product.selectedPrice
      );
      return calcDiscountValue(totalPrice, discount);
    },
    totalPrice() {
      return calcTotalPrice(this.product.qty, this.product.selectedPrice);
    }
  },
};
</script>

<style lang="scss" scoped>
.product-item-detail {
  border-bottom: 1px solid #eee;
}
.product-item {
  background: cadetblue;
  padding: 20px 15px;
}
.product-item-detail:hover {
  background: lightblue;
  cursor: pointer;
}
.discount {
  padding: 10px 15px;
}
</style>




